.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: 120px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}

.subtotal__button {
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  margin-top: 10px;
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  cursor: pointer;
}

@media only screen and (max-width: 648px) {
  .subtotal {
    width: 250px;
    height: 120px;
    padding: 20px;
  }
  .subtotal__button {
    width: 100%;
    height: 25px;
    margin-top: 10px;
  }
}

@media only screen and (max-width: 576px) {
  .subtotal {
    width: 200px;
    height: 120px;
    padding: 20px;
  }
  .subtotal__button {
    width: 100%;
    height: 20px;
    margin-top: 10px;
  }
}

@media only screen and (max-width: 468px) {
  .subtotal {
    margin-top: 10px;
  }
}
